
export const Generate = () =>  {
    console.log("Generating Social Cards...")

    // Now allowing downloads
    const dl = document.querySelector('#allowDl');
    dl.classList.remove("disabled");

    var imgsrc = document.getElementById("cropped").src;
    var image_width_actual = document.querySelector("#cropped").naturalWidth;
	var image_height_actual = document.querySelector("#cropped").naturalHeight;
    
    // $result.hide();
    document.getElementById("result").style.display = "none";
    document.querySelector("#images").style.display = "";

    // User Inputs
    // Check Spanish?
    const spanish = document.getElementById("TLMD");
    
    // .trim().replace(/ +(?= )/g,'') Removes spaces before and after text. Removes double spaces between letters
    var firstname = document.getElementById("firstname").value.trim().replace(/ +(?= )/g,'');
    var lastname = document.getElementById("lastname").value.trim().replace(/ +(?= )/g,'');
        if (firstname == null || firstname === "") {
            firstname = "Chuck"
        }
        if (lastname == null || lastname === "") {
            lastname = "Schumer"
        }
    var name = firstname + " " + lastname
    var local = document.getElementById("CheckLocal");
    var party = document.getElementById("PartySelect").value;
    var state = document.getElementById("StateSelect").value;
    var race = document.getElementById("RaceSelect").value;
        if (race == null || race === "") {
            if (spanish.checked) { // Spanish Check
                race = "Senador"
            }
            else {
                race = "U.S. Senate"
            }
        }
    var raceKey = ""

    var status = document.getElementById("StatusSelect").value;

    // Square Canvas
    const canvas = document.getElementById('igCanvas');
    const context = canvas.getContext('2d');

    // 16:9 Canvas
    const tCanvas = document.getElementById('twitterCanvas');
    const tContext = tCanvas.getContext('2d');

    // Story Canvas
    const sCanvas = document.getElementById('storyCanvas');
    const sContext = sCanvas.getContext('2d');

    // Loading Images
    // This draws images in order rather than multiple onloads all running at once
    // Ex: If the overlay loads before the image, they will stack out of order
    var env = process.env.PUBLIC_URL
    var sources = [imgsrc, env+"/images/sq-social-card-template.png", env+"/images/tw-social-card-template.png", env+"/images/story-social-card-template.png"]
        // Spanish Check - Updating Variables if Spanish is Checked
        if (spanish.checked) {
            sources = [imgsrc, env+"/es-images/sq-social-card-template.png", env+"/es-images/tw-social-card-template.png", env+"/es-images/story-social-card-template.png"]
            
            // Status
            let element = document.getElementById("StatusSelect");
            status = element.options[element.selectedIndex].getAttribute("data-es")

            // Race
            raceKey = ""
            let input_id = document.getElementById("RaceSelect");
            let datalist_id = document.getElementById("datalistOptions");
                for (var i=0;i<datalist_id.options.length;i++) {
                    if (datalist_id.options[i].value === input_id.value) {
                        race = datalist_id.options[i].getAttribute("data-es")
                        break;
                    }
                }
        }
    var imgCount = 0;
    const images = [];
   
    Object.entries(sources).forEach(element => {
        const img = new Image();
        img.src = element[1];
        images.push(img); // push to empty list
        imgCount ++; // add 1 to count
        img.onload = () => {
            imgCount --; // subtract 1 from count
            if (!imgCount) { 
                // Generate Square
                // drawImage(image, sx, sy, sWidth, sHeight, dx, dy, dWidth, dHeight);
                context.drawImage(images[0], 0, 0, image_width_actual, image_height_actual, 0, 0, 600, 600)
                context.drawImage(images[1], 0, 0, 1800, 1800, 0, 0, 1000, 1000)
                squareText()

                // Generate 16:9
                tContext.drawImage(images[0], 0, 0, image_width_actual, image_height_actual, 0, 0, 675, 675)
                tContext.drawImage(images[2], 0, 0, 1200, 675, 0, 0, 1200, 675)
                twitterText()

                // Generate Story 
                sContext.drawImage(images[0], 0, 0, image_width_actual, image_height_actual, 0, 0, 600, 600)
                sContext.drawImage(images[3], 0, 0, 540, 960, 0, 0, 540, 960)
                storyText()
            }
        }
    })

    // Wrap Text Function
    function wrapText(context, text, x, y, maxWidth, lineHeight) {
        var words = text.split(' ');
        var line = '';

        for(var n = 0; n < words.length; n++) {
          var testLine = line + words[n] + ' ';
          var metrics = context.measureText(testLine);
          var testWidth = metrics.width;
          if (testWidth > maxWidth && n > 0) {
            context.fillText(line, x, y);
            line = words[n] + ' ';
            y += lineHeight;
          }
          else {
            line = testLine;
          }
        }
        context.fillText(line, x, y);
      }

    // Square Overlay Text
    function squareText() {
        // Name
        context.fillStyle = "#4192ED";
        context.textAlign = "center";
        if (name.length >= 17) { // For long names: Alexandria Ocasio-Cortez / Cathy McMorris Rodgers
            context.font = "bold 40pt Flama Bold";
            context.fillText(firstname.toUpperCase() + " " + lastname.toUpperCase(), canvas.width/2, 465);
        }
        else {
            context.font = "bold 45pt Flama Bold";
            context.fillText(firstname.toUpperCase() + " " + lastname.toUpperCase(), canvas.width/2, 465);
        }
        
        // Party
        context.font = "20pt Flama Light";
        context.fillStyle = "#4192ED";
        context.textAlign = "center";
        context.fillText(party.toUpperCase(), canvas.width/2, 495);

        // Rectangle
        context.beginPath();
        context.fillStyle='#FFFF01';
        context.fillRect(100, 505, 400, 40);

        // Win Status
        if (spanish.checked && status === "Proyeccin ganador") {
            context.font = "bold 27pt Flama Bold";
        }
        else {
            context.font = "bold 30pt Flama Bold";
        }
        context.fillStyle = "#202124"; // was slategrey
        context.textAlign = "center";
        context.fillText(status.toUpperCase(), canvas.width/2, 540);
        
        // Race
        context.font = "35pt Flama Light";
        context.fillStyle = "#202124"; // was slategrey
        context.textAlign = "center";
        
        if (local.checked) {
            context.fillText(race.toUpperCase() + raceKey, canvas.width/2, 590);
        }
        else{
            context.fillText(state.toUpperCase() + " " + race.toUpperCase() + raceKey, canvas.width/2, 590);
        }
    };

    // 16:9 Overaly Text
    function twitterText() {
        var adj = 10

        // First Name
        tContext.fillStyle = "#4192ED";

        if (name.length >= 17) { // For long names: Alexandria Ocasio-Cortez / Cathy McMorris Rodgers
            tContext.font = "bold 55pt Flama Bold";
            tContext.fillText(firstname.toUpperCase(), 645, 260);
        }
        if (name.length <= 10) { // For short names: Don't split the name. All one line.
            tContext.font = "bold 55pt Flama Bold";
            tContext.fillText(name.toUpperCase(), 645, 315);
        }
        if (name.length > 10 & name.length < 17) {
            tContext.font = "bold 55pt Flama Bold";
            tContext.fillText(firstname.toUpperCase(), 645, 250);
        }

        // Last Name
        tContext.fillStyle = "#4192ED";
        if (name.length >= 17) { // For long names. split line. smaller last name.
            tContext.font = "bold 45pt Flama Bold";
            tContext.fillText(lastname.toUpperCase(), 645, 315);
        }
        else if (name.length <= 10) { // For short names. Do nothing. It's all one line above.
            // Do Nothing
        }
        else { // Everyone in between. Split line. Keep font size the same.
            tContext.font = "bold 55pt Flama Bold";
            tContext.fillText(lastname.toUpperCase(), 645, 315);            
        }

        // Party
        tContext.font = "25pt Flama Light";
        tContext.fillStyle = "#4192ED";
        tContext.fillText(party.toUpperCase(), 645, 355);

        // Rectangle
        tContext.beginPath();
        tContext.fillStyle='#FFFF01';
        tContext.fillRect(625, 385 - adj, 540, 50); // x, y, width, height

        // Win Status
        if (spanish.checked && status === "Proyeccin ganador") {
            tContext.font = "bold 35pt Flama Bold";
        }
        else {
            tContext.font = "bold 40pt Flama Bold";
        }
        tContext.fillStyle = "#202124"; // was slategrey
        tContext.fillText(status.toUpperCase(), 645, 430 - adj);
        
        // Race
        tContext.font = "50pt Flama Light";
        tContext.fillStyle = "#202124"; // was slategrey
        if (local.checked) {
            // wrapText(context, text, x, y, maxWidth, lineHeight);
            let text = race.toUpperCase() + raceKey;
            wrapText(tContext, text, 645, 500 - adj, 500, 60);
        }
        else {
            let text = state.toUpperCase() + " " + race.toUpperCase() + raceKey;
            wrapText(tContext, text, 645, 500 - adj, 500, 60);
        }
    };

    // Story Overlay Text
    function storyText() {
        var adj = 220

        // First Name
        sContext.fillStyle = "#4192ED";
        sContext.textAlign = "center";
        if (name.length >= 17) { // For long names: Alexandria Ocasio-Cortez / Cathy McMorris Rodgers
            adj = 225
            sContext.font = "bold 50pt Flama Bold";
            sContext.fillText(firstname.toUpperCase(), sCanvas.width / 2, 415 + adj);
        }
        else if (name.length <= 16) { // Everyone else
            sContext.font = "bold 50pt Flama Bold";
            sContext.fillText(name.toUpperCase(), sCanvas.width / 2, 465 + adj);
        }

        // Last Name
        sContext.fillStyle = "#4192ED";
        sContext.textAlign = "center";
        if (name.length >= 17) { // For long names. split line. smaller last name.
            sContext.font = "bold 45pt Flama Bold";
            sContext.fillText(lastname.toUpperCase(), sCanvas.width / 2, 470 + adj);
        }
        else if (name.length <= 16) { // Everyone else
            // Do Nothing
        }

        // Party
        sContext.font = "25pt Flama Light";
        sContext.fillStyle = "#4192ED";
        sContext.textAlign = "center";
        sContext.fillText(party.toUpperCase(), sCanvas.width / 2, 505 + adj);

        // Rectangle
        sContext.beginPath();
        sContext.fillStyle = '#FFFF01';
        sContext.fillRect(45, 525 + adj, 450, 50);

        // Win Status
        if (spanish.checked && status === "Proyeccin ganador") {
            sContext.font = "bold 35pt Flama Bold";
        }
        else {
            sContext.font = "bold 40pt Flama Bold";
        }
        sContext.fillStyle = "#202124"; // was slategrey
        sContext.textAlign = "center";
        sContext.fillText(status.toUpperCase(), sCanvas.width / 2, 570 + adj);

        // Race
        sContext.font = "45pt Flama Light";
        sContext.fillStyle = "#202124"; // was slategrey
        sContext.textAlign = "center";
        if (local.checked) {
            // wrapText(context, text, x, y, maxWidth, lineHeight);
            let text = race.toUpperCase() + raceKey;
            wrapText(sContext, text, sCanvas.width / 2, 635 + adj, 500, 60);
        }
        else {
            let text = state.toUpperCase() + " " + race.toUpperCase() + raceKey;
            wrapText(sContext, text, sCanvas.width / 2, 635 + adj, 500, 60);
        }
    };
    
}