import React, { useState } from "react";
import { createRoot } from 'react-dom/client'
import Cropper from "react-cropper";
import "cropperjs/dist/cropper.css";
import "./crop.css";
import { Row, Col, Button, ButtonGroup, Form, Alert } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';
import { Generate } from "./generate";
import { Download } from "./download";

export const Crop: React.FC = () => {
  const [image, setImage] = useState();
  const [cropData, setCropData] = useState("#");
  const [cropper, setCropper] = useState<any>();

  function AlertMsg() {
      return (
        <Alert variant="danger">
          <Alert.Heading className="bold">Oops! Forgot something?</Alert.Heading>
          <p>Please upload an image before trying to crop.</p>
        </Alert>
      );
  }

  // $("#result").hide()
  $("#images").hide()

  const onChange = (e: any) => {
    $("#alertBlock").hide()
    e.preventDefault();
    let files;
    if (e.dataTransfer) {
      files = e.dataTransfer.files;
    } else if (e.target) {
      files = e.target.files;
    }
    const reader = new FileReader();
    reader.onload = () => {
      setImage(reader.result as any);
    };
    reader.readAsDataURL(files[0]);
  };

  const getCropData = () => {
    // if (typeof cropper !== "undefined") {}
    try {
      setCropData(cropper.getCroppedCanvas().toDataURL());
      // Hide the crop tool after cropping
      // $(".cropper").addClass('hide') 
      const croptool = document.querySelector(".cropper") as HTMLElement | null;
      if (croptool != null) {
        croptool.style.display = "none";
      }
      $('#submit').removeClass('disabled'); // Enable the generate button
      $('#btnCrop').addClass('disabled'); // Disable crop button
    } catch (error) {
      // console.error("Here is the error:", error);
      // ReactDOM.render(<AlertMsg />, document.getElementById('alertBlock')); <---- ReactDOM.render() is no longer supported in v18
      createRoot(document.getElementById('alertBlock')!).render(<AlertMsg />)

    }
  };

  return (
    <div>
      <div style={{ width: "100%" }}>
          <Row>
            {/* Photo Upload */}
            <Col lg={4}>
              <Form.Group className="mb-2">
                <div className="input-group custom-file-button">
                <label className="input-group-text" htmlFor="fileInput">Upload Photo</label>
                  <Form.Control id="fileInput" 
                  type="file" 
                  accept="image/*" 
                  onChange={onChange}/>
                </div>
              </Form.Group>
            </Col>

            {/* Buttons */}
            <Col lg={6} className="mb-3">
            <ButtonGroup className="pad" aria-label="Basic example">
              <Button variant="outline-primary" type="button" id="btnCrop" value="Crop" onClick={getCropData}>Crop</Button>
              <Button className="disabled" variant="outline-primary" type="button" id="submit" onClick={Generate}>Generate Cards</Button>
              <Button className="disabled" variant="outline-primary" type="button" id="allowDl" onClick={Download}>Download</Button>
            </ButtonGroup>
            </Col>
            
          </Row>
          <div id="alertBlock"></div>
          
        <div className="cropper">
          <Cropper
            style={{ height: 600, width: "100%" }}
            initialAspectRatio={1}
            aspectRatio={1}
            preview=".img-preview"
            src={image}
            viewMode={2}
            minCropBoxHeight={300}
            minCropBoxWidth={300}
            background={false}
            responsive={true}
            autoCropArea={0.9} // .9 = 90%, 1 = 100%
            checkOrientation={false}
            onInitialized={(instance) => {
              setCropper(instance);
            }}
            guides={true}
          />
        </div>
      </div>

      {/* Crop Tool */}
      <div className="row"> 
        <div className="col-md-12">
          <div id="result">
            <img crossOrigin='anonymous' id="cropped" src={cropData} alt="" />
          </div>
        </div>
      </div> 
      {/* Crop Tool Closed */}

      {/* Generated Social Cards */}
      <div id="images">
      <div className="row">
        <div className="col-lg-12 mb-3">
          <canvas id="twitterCanvas" width="1200px" height="675px"></canvas>
        </div>
      </div>
      <div className="row"> 
        <div className="col-xl-6">
          <canvas id="storyCanvas" width="540px" height="960px"></canvas>
        </div>
        <div className="col-xl-6">
          <canvas id="igCanvas" width="600px" height="600px"></canvas>
        </div>
      </div> 
      </div>
      {/* Generated Social Cards Closed */}

    </div>
    // Crop Closed
  );
};

export default Crop;
