import { saveAs } from 'file-saver';
import $ from 'jquery';
var JSZip = require("jszip");

export const Download = () =>  {

    console.log("Starting Download")

    // Setup
    // Filename based on user input name
    var firstname = document.getElementById("firstname").value.trim().replace(/ +(?= )/g,'');
    var lastname = document.getElementById("lastname").value.trim().replace(/ +(?= )/g,'');
    if (firstname == null || firstname === "") {
        firstname = "Chuck"
    }
    if (lastname == null || lastname === "") {
        lastname = "Schumer"
    }
    var name = firstname + " " + lastname

    // Canvases
    var image = document.getElementById("igCanvas")
    var twimage = document.getElementById("twitterCanvas")
    var stimage = document.getElementById("storyCanvas")

    // HTML Canvas -> Base64 Image Function
    function getBase64Image(img) {
        var dataURL = img.toDataURL("image/jpg");
        return dataURL.replace(/^data:image\/(png|jpg);base64,/, "");
    }

    var igCard = getBase64Image(image) // Run canvas through function
    var twitCard = getBase64Image(twimage)
    var storyCard = getBase64Image(stimage)

    // Zip Files
    var zip = new JSZip();
    zip.file(name + " - IG Social Card.jpg", igCard, {base64: true});
    zip.file(name + " - Twitter Social Card.jpg", twitCard, {base64: true});
    zip.file(name + " - IG Story Social Card.jpg", storyCard, {base64: true});

    console.log("Zipped", zip)

    // Download
    zip.generateAsync({type:"blob"}).then(function (blob) {
        saveAs(blob, name + " - Social Cards.zip");
        console.log("Saving")
        console.log("Downloaded")
    }, function (err) {
        $("#download").text(err);
        console.log("Error!")
    });
}